<template>
  <v-flex xs12 flexbox @click.stop="play" :key="song.trackID">
    <v-card>
      <v-card-media v-lazy:background-image="song.posterLarge" height="220px">
        <v-container grid-list-xs fill-height fluid>
          <v-layout fill-height>
            <v-flex xs12 align-end flexbox>
              <span class="headline white--text" v-text="song.title"></span>
              <div class="artist white--text" v-text="song.artist"></div>
              </v-flex>
          </v-layout>
        </v-container>
      </v-card-media>
      <v-card-actions>
          <v-btn icon @click.stop.native="show = !show">
            <v-icon>{{ show ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>share</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>file_download</v-icon>
          </v-btn>          
          <v-btn icon>
            <v-icon></v-icon>
          </v-btn>                    
        </v-card-actions>
        <v-slide-y-transition v-if="show">
          <v-card-text class="desc" v-html="ytTimeToSeconds(desc)">
        </v-card-text>
      </v-slide-y-transition>      
    </v-card>
  </v-flex>
</template>
<script>

export default {
  props: ['song', 'index'],
  name: 'current-playlist-item',
  data () {
    return {
      show: false,
      desc: ''
    }
  },
  computed: {
  },
  watch: {
    'show': 'ifShowGetDesc'
  },
  filters: {
    ytTimeToSeconds: function (value) {
      if (!value) {
        return ''
      }
      return (value.replace(/\n/g, '<br>').replace(/(?:(?:([01]?\d|2[0-3]):)?([0-5]?\d):)([0-5]?\d)/g, `<a href='' @click='$emit('ytime', $&)' clas='yt-time'>$&</a>`))
    }
  },
  methods: {
    ytTimeToSeconds: function (value) {
      if (!value) {
        return ''
      }
      return (value.replace(/\n/g, '<br>').replace(/(?:(?:([01]?\d|2[0-3]):)?([0-5]?\d):)([0-5]?\d)/g, `<a href="" id="yt-time">$&</a>'`))
    },
    ifShowGetDesc: function (newVal) {
      if (newVal) {
        this.getDesc()
      }
    },
    getDesc: function () {
      if (this.song.source === 'YouTube') {
        console.log('getting description')
        // this.desc = this.song.desc
        this.$DCAPI.getSongDescription(this.song.trackID, this.song.source, (resp) => {
          console.log('got yt desc', resp.items[0].snippet.description)
          this.desc = resp.items[0].snippet.description
        })
      } else {
        this.desc = this.song.desc
      }
    },
    play: function () {
      this.$parent.$parent.play(this.index + this.$store.getters.index)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.desc{
  word-break: break-all
}
.card__media[lazy=loading] {
  background: center center / cover no-repeat;
}
.card__media[lazy=loaded] {
  background: center center / cover no-repeat;
}
</style>



